.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.ime-mode-disabled {
  ime-mode: disabled;
  -ms-ime-mode: disabled;
}

.alert-text {
  color: red;
  padding-top: 5px;
}

.contents {
  padding: 0;
  overflow: hidden;
}

.container{
  overflow: hidden;
}

.validation-error {
  border: 0px solid #f00;
  color:#f00;
  font-size: 12pt;
}

.order-created-info {
  text-align: center;
  color:#f00;
  font-size: 12pt;
}

.thank-you-header {
  background-color: #3399FF;
  color: white;
  padding: 15px 10px 15px 10px;
  font-size: 1.2em;
}

.thank-you-body {
  padding: 5px 10px 15px 10px;
  font-size: 1.15em;
  border-style: solid;
  border-width: 0px 1px 1px 1px;
  border-color: lightgray;
}

.nav-info-text {
  font-size: 0.9em;
  color: black !important;
}

.order-creating-container {
  padding: 5px 0px 15px 0px;
}

.order-creating-body{
  padding: 5px 10px 15px 10px;
  font-size: 1.15em;
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  border-color: lightgray;
}